import { Component, ElementRef, ViewChild, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { HubConnectionBuilder } from "@aspnet/signalr";
import Guacamole, { Tunnel } from "guacamole-common-js";
import { SignalRTunnel } from "./common/signalr.tunnel";
import { MsAdalAngular6Service } from "microsoft-adal-angular6";
import { connect } from 'tls';
@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent implements OnInit {
  title = "guacamole-angular";

  @ViewChild("displayElement")
  displayElement: ElementRef;

  constructor(private adalSvc: MsAdalAngular6Service, httpClient: HttpClient) {
    httpClient
      .get("https://localhost:44308/api/v1.0/me/devices", {
        headers: { Authorization: "Bearer " + this.adalSvc.accessToken }
      })
      .subscribe(o => {
        console.log(o);
      });

  }

  private getGraphApiToken() {
    return this.adalSvc
      .acquireToken("https://graph.microsoft.com")
      .toPromise();
  }

  private getTunnel() {
    return new SignalRTunnel(builder => {
      builder.withUrl("https://localhost:44308/ws/", {
        accessTokenFactory: () => this.adalSvc.accessToken
      });
      return builder;
    });
  }

  private connect() {
    const client = new Guacamole.Client(this.getTunnel());

    this.displayElement.nativeElement.appendChild(
      client.getDisplay().getElement()
    );

    const mouse = new Guacamole.Mouse(client.getDisplay().getElement());
    const display = client.getDisplay();

    mouse.onmousedown = mouse.onmouseup = mouse.onmousemove = function(mouseState) {
      client.sendMouseState(
        new Guacamole.Mouse.State(
          mouseState.x / display.getScale(),
          mouseState.y / display.getScale(),
          mouseState.left,
          mouseState.middle,
          mouseState.right,
          mouseState.up,
          mouseState.down
        )
      );
    };

    display.getElement().onclick = function(e) {
      e.preventDefault();
      return false;
    };

    const keyboard = new Guacamole.Keyboard(document);
    keyboard.onkeydown = function(keysym) {
      client.sendKeyEvent(1, keysym);
    };
    keyboard.onkeyup = function(keysym) {
      client.sendKeyEvent(0, keysym);
    };

    client.onerror = function(error) {
      console.error(error);
    };

    client.connect({ deviceId: 1 });
  }
  ngOnInit(): void {
    this.connect();
  }
}
